import Moment from "moment";
import queryString from "query-string";
import "./Participate.scss";
import React, { useState, useEffect } from "react";
import { Route, Redirect, BrowserRouter, Switch } from "react-router-dom";
import med from "./images/med.svg";
import recc from "./images/mic.png";
import reset from "./images/reset.png";
import Recorder from "@bigear/microphone-recorder";
import axios from "axios";
const AudioContext = window.AudioContext || window.webkitAudioContext;

export default function Participate(props) {
  const [state, setState] = useState({
    recorder: "",
    audio: null,
    audioContext: "",
    audioFileName: "",
    audioFileSelected: false,
    recordingStatus: false,
    buildingAudio: false,
  });

  const [recclass, setRecclass] = useState("nored");

  const [details, setDetails] = useState({
    Name: "",
    Phone: "",
    Time: "",
  });
  const [rec, setRec] = useState({
    disabled: "enabled",
    recStart: "Colorless",
  });

  console.log(state);

  useEffect(() => {
    const values = queryString.parse(props.location.search);
    // http://localhost:3000/sub/?Name=John%20Doe&Phone=9989122130

    setDetails({
      Name: values.Name,
      Phone: values.Phone,
    });
  }, []);

  const startRecording = () => {
    setRecclass("redb");
    setRec({ ...rec, disabled: "disabled", recStart: "Color" });
    console.log("startRecording");
    setState({ ...state, recordingStatus: true });
    navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then((mediaStreamObject) => {
        const audio_context = new AudioContext();
        const input = audio_context.createMediaStreamSource(mediaStreamObject);

        const recorder = new Recorder(input);
        recorder.record();

        console.log(recorder);
        setState({
          ...state,
          recorder,
          audio: input,
          audioContext: audio_context,
          audioFileName: "",
          audioFileSelected: false,
        });

        console.log(state);
      })

      .catch((error) => {
        setState({ ...state, recordingStatus: false });
      });
  };

  const stopRecording = () => {
    setRecclass("nored");
    console.log(state.recorder);
    console.log("stop recording");
    setState({ ...state, recordingStatus: false });
    setState({ ...state, buildingAudio: true });
    state.recorder.stop();
    setState({ ...state, audio: null });
    const saveRecord = (record) => {
      console.log(record);
      const date = new Date();
      const timestamp = Moment(date).format("YYYY-MM-DD HH:mm:ss");
      console.log(timestamp);
      const audioFileName = details.Name + timestamp + ".wav";
      setState({ ...state, record, buildingAudio: false, audioFileName });
      console.log("File name is", audioFileName);
      state.recorder.clear();
    };
    state.recorder.exportWAV("audio/wav", function (blob) {
      // append the audio blob to html element
      console.log(blob);
      // const url = URL.createObjectURL(blob);
      saveRecord(blob);
    });
    alert("Recording saved. Please click Submit");
  };

  const uploadFile = () => {
    const formData = new FormData();
    formData.append("mobile_number", details.Name);
    formData.append("user_name", details.Phone);
    formData.append("timestamp", "2019-01-01 11:12:13");
    formData.append("coughsoundfile", state.record, state.audioFileName);

    axios({
      method: "POST",
      url: "https://beta.dhas.in/api/coviddatacollection",
      headers: {
        accessToken: localStorage.accessToken,
        "Content-Type": "multipart/form-data",
      },
      data: formData,
    })
      .then((response) => {
        console.log(response);
        alert("Success! Recording sent.");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className="pa-root">
      <div className="pa-head">Contribute your samples to Swaasa Research</div>

      <div className="par-body">
        <div className="med">
          <img src={med} className="medimg" />
        </div>
        <div className="par-inputs">
          <div>
            <input
              className="par-name"
              placeholder="Full Name"
              defaultValue={details.Name}
              onChange={(e) =>
                setDetails({
                  ...details,
                  Name: e.target.value,
                })
              }
            ></input>
          </div>
          <div>
            <input
              className="par-name"
              placeholder="Mobile Number"
              defaultValue={details.Phone}
              onChange={(e) =>
                setDetails({ ...details, Phone: e.target.value })
              }
            ></input>
          </div>
          <div className="pa-btns">
            <div className="red" onClick={startRecording}>
              {/* <button>Record</button> */}
              <button className={recclass}>Record</button>
            </div>
            <div className="res" onClick={stopRecording}>
              <button>Stop</button>
            </div>
          </div>
          {recclass == "redb" ? (
            <div className="recstatus">Recording..</div>
          ) : (
            <p>{details.audioFileName} </p>
          )}
          <div className="instru">
            STEP 1: Click on record to start recording for 10secs
          </div>
          <div className="instru">
            STEP 2: You can click the reset button to start again if needed
          </div>
          <div className="instru">
            STEP 3: Click on the "Submit" button to send sample to us
          </div>
        </div>
      </div>
      <div className="par-submit">
        <button className="submit-btn" onClick={uploadFile}>
          SUBMIT
        </button>
      </div>
    </div>
  );
}
