export const initializaFirebase = () => {
  firebase.initializeApp(firebaseConfig);
  navigator.serviceWorker
    .register("firebase-messaging-sw.js")
    .then((registration) => {
      firebase.messaging().useServiceWorker(registration);
    });
};

export const askForPermissionToReceiveNotifications = async () => {
  try {
    const messaging = firebase.messaging();
    await messaging.requestPermission();
    const token = await messaging.getToken();
    console.log("firebase token: ", token);

    localStorage.firebaseToken = token;
    messaging.onMessage((payload) => {
      console.log(payload);
      const receivedData = JSON.parse(payload.data.default);
      console.log(receivedData);
      const record = JSON.parse(receivedData.record);
      console.log(record);
      if (receivedData.message === "Assessment generated successfully") {
        NotificationManager.success(
          "Assessment generated successfully",
          record.recordId,
          3000,
          () => {
            window.location = "/viewReport/" + record.recordId;
          }
        );
      } else {
        NotificationManager.success(
          "Unable to generate assessment",
          "Error",
          3000
        );
      }
    });
  } catch (error) {
    console.error(error);
  }
};
